<template>
  <v-toolbar color="primary" fixed dark app>
    <v-toolbar-title class="ml-0 pl-3"> </v-toolbar-title>
    <v-toolbar-side-icon @click.stop="handleDrawerToggle"></v-toolbar-side-icon>
    <v-text-field
      flat
      solo-inverted
      prepend-icon="search"
      label="Search"
      name="Search"
      class="hidden-sm-and-down"
      v-model="searchedInput"
      v-on:keyup="validateResearch"
    >
    </v-text-field>
    <v-spacer></v-spacer>
    <v-toolbar-items>

      <v-btn flat @click="contactus" class="">
        Contact Us
      </v-btn>
    </v-toolbar-items>


    <v-btn icon @click="handleFullScreen()">
      <v-icon>fullscreen</v-icon>
    </v-btn>
    <v-menu
      offset-y
      origin="center center"
      class="elelvation-1"
      :nudge-bottom="14"
      transition="scale-transition"
    >
      <v-btn icon flat slot="activator">
        <v-badge color="red" overlap>
          <span slot="badge">3</span>
          <v-icon medium>notifications</v-icon>
        </v-badge>
      </v-btn>
      <notification-list></notification-list>
    </v-menu>
    <v-menu
      offset-y
      origin="center center"
      :nudge-bottom="10"
      transition="scale-transition"
    >
      <v-btn icon large flat slot="activator">
        <v-avatar size="30px">
          <img src="/static/avatar/man_4.jpg" alt="Michael Wang" />
        </v-avatar>
      </v-btn>
      <v-list class="pa-0">
        <v-list-tile
          v-for="(item, index) in items"
          :to="!item.href ? { name: item.name } : null"
          :href="item.href"
          @click="item.click"
          ripple="ripple"
          :disabled="item.disabled"
          :target="item.target"
          rel="noopener"
          :key="index"
        >
          <v-list-tile-action v-if="item.icon">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>
  </v-toolbar>


</template>
<script>
import NotificationList from "@/components/widgets/list/NotificationList";
import Util from "@/util";
export default {
  name: "app-toolbar",
  components: {
    NotificationList
  },
  data: () => ({
    items: [
      {
        icon: "account_circle",
        href: "",

        title: "Profile",
        click: e => {
            //this.$router.push("../personnalpage");
          window.getApp.$emit("APP_PERSONAL_PAGE");
        }
      },
      {
        icon: "settings",
        href: "#",
        title: "Settings",
        click: e => {
          window.getApp.$emit("APP_SETTINGS");
        }
      },
      {
        icon: "fullscreen_exit",
        href: "#",
        title: "Logout",
        click: e => {
          window.getApp.$emit("APP_LOGOUT");
        }
      }
    ]
  }),
  computed: {
    toolbarColor() {
      return this.$vuetify.options.extra.mainNav;
    }
  },
  methods: {
    validateResearch: function(e) {
      if (e.keyCode === 13) {
        var inputedText = this.searchedInput; // la variable inputedText contient la phrase entrée dans la barre de recherche
       // alert(inputedText);
       // getQuickSearch(inputedText);
        appel_ajax(inputedText);
      }
      this.log += e.key;
    },

    handleDrawerToggle() {
      window.getApp.$emit("APP_DRAWER_TOGGLED");
    },
    handleFullScreen() {
      Util.toggleFullScreen();
    },

    contactus() {
      this.loading = true;
      setTimeout(() => {
        this.$router.push("../contactus");
      }, 1000);
    },


    getQuickSearch(param) {

      var request = new XMLHttpRequest();
     
      var url = "http://mdl-std02.info.fundp.ac.be:8181/MdlGroupe2-test/api/QuickSearch?keyword="+param;

      // Modifier le deuxième argument en fct de ce qu'on veut récup
      request.open('GET', url, true);
      request.onload = function() {
        // Begin accessing JSON data here
        var data = JSON.parse(this.response);

        if (request.status >= 200 && request.status < 400) {
          //data.forEach(word => {
            //Same here (idem commentaire ligne 152)
           // console.log(word);
         // });
        } else {
          console.log('error')
        }
      }

      request.send();

    }

  }
};

function appel_ajax(param){
   
 var xhr = getXhr();
 // On défini ce qu'on va faire quand on aura la réponse
 xhr.onreadystatechange = function(){
   // On ne fait quelque chose que si on a tout reçu et que le serveur est ok
   if(xhr.status < 400 && xhr.status >= 200){
     //Ici sera afficher le résultat de notre script PHP ajax.php  alert(xhr.responseText);
     //alert(xhr.responseText);
     alert(param);
     //xhr.responseText;
   }
 }
 var param1 =param;
 var url = "http://mdl-std02.info.fundp.ac.be:8181/MdlGroupe2-test/api/QuickSearch?keyword="+param1;
 xhr.open("GET",url,true) ;
 xhr.send(null);
}

function getXhr(){
 var xhr = null;
 if(window.XMLHttpRequest) // Firefox et autres
   xhr = new XMLHttpRequest();
 else if(window.ActiveXObject){ // Internet Explorer
   try {
     xhr = new ActiveXObject("Msxml2.XMLHTTP");
   } catch (e) {
     xhr = new ActiveXObject("Microsoft.XMLHTTP");
   }
 }
 else { // XMLHttpRequest non supporté par le navigateur
   alert("Votre navigateur ne supporte pas les objets XMLHTTPRequest...");
   xhr = false;
 }
 return xhr
}
</script>
